<template>
  <el-dialog :title="dialogTitle">
    <div>
      <!-- <h2>{{ item.name }}</h2> -->

      <img class="item-icon-image" :src="item.icon" />

      <h4>{{ item.title }}</h4>
      <p>{{ item.description }}</p>
    </div>

    <div>
      <el-button type="success"> 安装 </el-button>
      <el-button type="danger"> 卸载 </el-button>
      <el-button> 升级 </el-button>
      <el-button> 重新安装 </el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "settings-plug-ins-item-detail-dialog",

  computed: {
    dialogTitle() {
      return this.item.name + " - 插件详情";
    },
  },

  data() {
    return {
      item: {},
    };
  },

  props: {
    target: Object,
  },

  watch: {
    target(value) {
      if (value == null) {
        return;
      }
      this.item = value;
    },
  },
};
</script>

<style scoped>
.item-icon-image {
  max-width: 96px;
  max-height: 96px;
  border-color: gray;
  border-style: solid;
  border-width: 1px;
}
</style>
